import React from "react";
import logo from "../img/Withdraw.png";
import "./dis3.css";

export default function Dis3() {
  return (
    <div className="dis3">
      <div className="image_area2">
        <img src={logo} alt="text" className="logo3" />
      </div>
      <div className="text_area3">
        <h1>
          Easily <span>withdraw</span> your money with no questions asked
        </h1>
        <p className="para3">
          Our app gives you the feature to easily withdraw any amount of money
          from your stocks with just a tap of a button.
        </p>
      </div>
    </div>
  );
}
