import React from "react";
import logo from "../img/Leaderboard.png";
import "./dis2.css";

export default function Dis2() {
  return (
    <div className="dis2">
      <div className="image_area2">
        <img src={logo} alt="text" className="logo2" />
      </div>
      <div className="text_area2">
        <h1>
          Know your friends real time
          <span> scores </span>
        </h1>
        <p className="para2">
          Know your position and compare your position with your friends
        </p>
      </div>
    </div>
  );
}
