import React from "react";
import logo from "../img/portfolio.png";
import "./dis1.css";

export default function Dis1() {
  return (
    <div className="dis1">
      <div className="text_area">
        <h1>
          Create a stunning <span> portfolio </span>
          with our experts help
        </h1>
        <p className="para1">
          Our experts will guide you whenever you are in doubt. Your desicion to
          buy or sell a stock will be now easier.
        </p>
      </div>
      <div className="image_area">
        <img src={logo} alt="text" className="logo" />
      </div>
    </div>
  );
}
